import CheckBox from "@mui/icons-material/CheckBox";
import CheckBoxOutlineBlank from "@mui/icons-material/CheckBoxOutlineBlank";
import CloseIcon from "@mui/icons-material/CloseOutlined";
import ExpandLessOutlined from "@mui/icons-material/ExpandLessOutlined";
import ExpandMoreOutlined from "@mui/icons-material/ExpandMoreOutlined";
import SaveIcon from "@mui/icons-material/Save";
import { LoadingButton } from "@mui/lab";
import {
    Box, Collapse, DialogActions,
    DialogTitle,
    Divider, IconButton, List, ListItemButton, ListItemIcon, ListItemText, Typography
} from "@mui/material";
import { useEffect, useState } from "react";
import { useMiddleware } from "../../../Application";
import { useAuth } from "../../../auth";
import ResponsiveDialog from '../../../components/ResponsiveDialog';
import TextField from "../../../components/TextField";
import useFetch from "../../../utils/useFetch";
import useForm from '../../../utils/useForm';


const PermissionSelectItem = ({ module, value = [], onChange = () => { } }) => {

    const [open, setOpen] = useState(false);
    const toggle = () => setOpen(!open);

    const isSelected = item => value.filter(v => v.id === item.id).length > 0;
    const handleSelect = item => {
        onChange(
            isSelected(item) ?
                value.filter(v => v.id !== item.id) :
                [...value, item]
        )
    }


    return (
        <>
            <ListItemButton onClick={toggle}>
                <ListItemText primary={<strong>{module.name}</strong>} />
                {open ? <ExpandLessOutlined /> : <ExpandMoreOutlined />}
            </ListItemButton>
            <Collapse in={open} timeout="auto" unmountOnExit>
                <List dense component="div" disablePadding>
                    {module.permissions.map((permission, index) => (
                        <ListItemButton
                            key={index}
                            sx={{ pl: 4 }}
                            onClick={() => handleSelect(permission)}
                        >
                            <ListItemIcon>
                                {isSelected(permission) ? <CheckBox /> : <CheckBoxOutlineBlank />}
                            </ListItemIcon>
                            <ListItemText 
                                primary={permission.name} 
                                // secondary='ایجاد فایل های مختلف در تمامی ماژول ها'
                            />
                        </ListItemButton>
                    ))}
                </List>
            </Collapse>
        </>
    )
}

const AccessSelect = ({ value, onChange }) => {


    const middleware = useMiddleware('Core.Workspace');


    const { data, loading, reload } = useFetch({
        handler: () => middleware.modules({
            whereHasPermissions: true
        }),
        initialState: {
            data: { data: [], total: 0 },
            loading: true
        }
    });

    const { data: modules } = data;


    if (loading) {
        return <Typography
            variant="subtitle2"
            fontWeight={400}
            sx={{ p: 2, textAlign: 'center' }}
        >در حال بارگذاری...</Typography>
    }

    return (
        <List dense>
            {modules.map((module, index) => (
                <PermissionSelectItem
                    key={index}
                    module={module}
                    value={value}
                    onChange={onChange}
                />
            ))}
        </List>
    )
}


const RoleFormDialog = ({
    open,
    onClose,
    defaultValues = {},
    onCreateOrUpdate
}) => {

    const { inject } = useAuth();

    const middleware = useMiddleware('Roles.Main');

    const initialValues = {
        name: '',
        access: [],
        ...Object.assign({}, ...(inject('Roles', 'RoleForm.initialValues') || [])),
        ...defaultValues
    }

    const {
        register,
        loading,
        handleSubmit,
        reset,
        getValue,
        setValue
    } = useForm({
        initialValues,
        handlePost: data => middleware.createOrUpdate(data),
        onSubmit: onCreateOrUpdate
    })

    useEffect(() => {
        if (open) reset(initialValues);
    }, [open]);

    const injectedHooks = Object.assign({}, ...(inject('Roles', 'RoleForm.Hooks') || []));


    return (
        <ResponsiveDialog
            open={open}
            onClose={onClose}
            dialogProps={{
                fullWidth: true,
                maxWidth: "xs"
            }}
        >
            <form
                noValidate
                onSubmit={handleSubmit}
            >
                <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Box>{defaultValues.id ? 'بروزرسانی مشخصات نقش' : 'ثبت مشخصات نقش'}</Box>
                    <IconButton onClick={onClose} size="small">
                        <CloseIcon />
                    </IconButton>
                </DialogTitle>
                <Divider />
                <Box sx={{ p: 0 }}>
                    <Box sx={{ p: 2 }}>
                        <TextField
                            InputLabelProps={{ shrink: true }}
                            fullWidth={true}
                            size="small"
                            variant="filled"
                            label="نام نقش"
                            placeholder="مدیر کل، مدیر فروش، منشی دفتر یا..."
                            required
                            {...register('name')}
                        />
                    </Box>
                    <Divider />
                    <Typography fontWeight="bold" sx={{ m: 2, mb: 0 }}>مجوزهای نقش</Typography>
                    <AccessSelect
                        value={getValue('access').map(acc => acc.permission)}
                        onChange={value => setValue('access', value.map(permission => ({ permission })))}
                    />
                    {inject('Roles', 'RoleForm.PostForm', { register, loading, getValue, setValue, reset, ...injectedHooks })}
                </Box>
                <Divider />
                <DialogActions>
                    <LoadingButton
                        loading={loading}
                        loadingPosition="start"
                        type="submit"
                        variant="contained"
                        startIcon={<SaveIcon />}
                    >
                        ثبت مشخصات
                    </LoadingButton>
                    <Box sx={{ flexGrow: 1 }}></Box>
                </DialogActions>
            </form>

            {inject('Roles', 'RoleForm.Footer', { register, loading, getValue, setValue, reset, ...injectedHooks })}
        </ResponsiveDialog>
    )
}

export default RoleFormDialog