import AddIcon from "@mui/icons-material/Add";
import MenuOutlined from "@mui/icons-material/MenuOutlined";
import { Box, Card, Divider, Stack } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { useSearchParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import useDialogProps from "../../../../utils/useDialogProps";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
import RoleDeleteDialog from "../../components/RoleDeleteDialog";
import RoleFormDialog from "../../components/RoleFormDialog";
import DataTable from "./DataTable";
import FilterForm from "./FilterForm";


const Roles = () => {

    const { inject } = useAuth();

    const middleware = useMiddleware('Roles.main');

    const [searchParams] = useSearchParams();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.list({
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    })

    const { enqueueSnackbar } = useSnackbar();

    const [formDialogProps, openFormDialog, closeFormDialog] = useDialogProps("form");
    const handleCreateOrUpdate = (message) => {
        enqueueSnackbar(message);
        reload();
        closeFormDialog();
    }

    const [deleteDialogProps, openDeleteDialog, closeDeleteDialog] = useDialogProps("delete", {
        hashDeps: ["role"]
    });
    const handleDelete = () => {
        enqueueSnackbar('نقش حذف گردید');
        closeDeleteDialog();
        reload();
    }

    const injectedHooks = Object.assign({}, ...(inject('Roles', 'RolesList.Hooks') || []));

    const task = useTask('Roles.manage');


    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Box
                    sx={{
                        display: "flex",
                        mb: { xs: 0, sm: 1 }
                    }}
                >
                    <Box sx={{ flexGrow: 1 }} />
                    <Stack direction="row" spacing={1}>
                        <ResponsiveActionBar
                            actions={sortBy([
                                {
                                    title: 'نقش جدید',
                                    icon: <AddIcon />,
                                    onClick: () => {
                                        openFormDialog({
                                            onCreateOrUpdate: () => handleCreateOrUpdate(`نقش ثبت گردید.`)
                                        })
                                    },
                                    priority: 100
                                },
                                ...flatten((inject('Roles', 'RolesList.ActionsList', { data, loading, reload, ...injectedHooks }) || []))
                            ], o => o.priority)}
                            ResponsiveActionIcon={<MenuOutlined />}
                            responsiveActionProps={{ color: 'primary' }}
                        />
                    </Stack>
                </Box>
                <Card variant="outlined" sx={{ flexGrow: 1 }}>
                    <FilterForm />
                    <Divider />
                    <DataTable
                        roles={data}
                        loading={loading}
                        meta={{ total }}
                        infiniteScrollProps={{
                            loading: false,
                            hasNextPage: data.length < total,
                            onLoadMore: loadMore
                        }}
                        onClickEdit={item => {
                            openFormDialog({
                                defaultValues: item,
                                onCreateOrUpdate: () => handleCreateOrUpdate(`نقش بروزرسانی گردید.`)
                            })
                        }}
                        onClickDelete={(role) => {
                            openDeleteDialog({ role });
                        }}
                    />
                </Card>

                <RoleFormDialog
                    {...formDialogProps}
                />

                <RoleDeleteDialog
                    {...deleteDialogProps}
                    onDelete={handleDelete}
                />

                {inject('Roles', 'RolesList.Footer', { data, loading, reload, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}


export default Roles