import DeleteOutline from "@mui/icons-material/DeleteOutline";
import Edit from "@mui/icons-material/EditOutlined";
import { ListItemButton, ListItemText } from "@mui/material";
import { useState } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";


const DataTable = ({
    loading,
    roles,
    meta,
    infiniteScrollProps,
    onClickEdit,
    onClickDelete,
}) => {

    const navigate = useNavigate();
    const location = useLocation();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();


    return (
        <>
            <ResponsiveTable
                data={roles}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={[
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => <Text>{getRowNumber(index)}</Text>
                    }, {
                        field: 'code',
                        title: 'نقش',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        render: row => <Text>{row.name}</Text> || '-',
                    }, {
                        title: 'مجوز‌ها',
                        rowCellProps: {
                            width: 400
                        },
                        render: row => (
                            <Text>{row.access.map(acc => acc.permission.name).join(', ')}</Text>
                        )
                    }
                ]}
                renderSheetTitle={row => <Text>{row.subject}</Text>}
                renderListItemChildren={(row) => (
                    <ListItemButton
                        onClick={() => navigate(
                            '/roles/'.concat(row.id),
                            { state: { prev: location } }
                        )}
                    >
                        {/* <ListItemAvatar>
                            <Avatar src={row.avatar} />
                        </ListItemAvatar> */}
                        <ListItemText
                            primary={<strong>{row.fullName}</strong>}
                        // secondary={row.roles && row.roles.length > 0 ? <Text>{row.roles.map(r => r.name).join(', ')}</Text> : '-'}
                        />
                    </ListItemButton>
                )}

                tableRowProps={row => ({
                    hover: true,
                    striped: true,
                    onClick: () => navigate(
                        '/roles/'.concat(row.id),
                        { state: { prev: location } }
                    )
                })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.get('perPage') || 25,
                    page: searchParams.get('page') || 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

                actions={[
                    { title: 'ویرایش', icon: <Edit />, onClick: onClickEdit },
                    { title: 'حذف', icon: <DeleteOutline />, onClick: person => onClickDelete(person, false) }
                ]}
            />
        </>
    )
}

export default DataTable